import React, { useState, useEffect } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import axios from 'axios';
import '../css/Tourist.scss';
import touristJson from './jsonFile/tourist.json';

const Tourist = () => {
    const [regionFilter, setRegionFilter] = useState('');
    const [categoryFilter, setCategoryFilter] = useState('');
    const [hashtagOptions, setHashtagOptions] = useState([]);
    const [currentPage, setCurrentPage] = useState(1);
    const [touristData, setTouristData] = useState([]);
    const [loading, setLoading] = useState(false);
    const [searchKeyword, setSearchKeyword] = useState('');
    const [totalCount, setTotalCount] = useState(0);  // totalCount  
    const [arrange, setArrange] = useState('O');
    const navigate = useNavigate();

    // regions    
    const regionOptions = Object.entries(touristJson.regions).map(([code, label]) => ({
        value: code,
        label: label,
    }));

    // categorys    
    const categoryOptions = Object.entries(touristJson.categorys).map(([code, label]) => ({
        value: code,
        label: label,
    }));

    //    
    const getRegionName = (regionCode) => {
        return touristJson.regions[regionCode] || '  '; //      '  '
    };

    //    (cat2  )
    const getHashtag = (cat2) => {
        return touristJson.categorys[cat2] || ''; //  category   ''
    };

    //  URL  
    const encodeUTF8 = () => {
        return encodeURIComponent(searchKeyword.trim());
    };

    //   
    const handleSearch = () => {
        setLoading(true); //      true 
        console.log('AXIOS ');
        const currentEncodedData = encodeUTF8();
        const data = {
            keyword: currentEncodedData,  //     .
            pageNo: currentPage,
            hashtag: categoryFilter,  //    
            regionCode: regionFilter,
            arrange: arrange,  //   
            contentTypeId: '12',  //   
        };

        axios.post('http://localhost:9000/api/getSearch', data, {
          
            headers: {
                'Content-Type': 'application/json', // Content-Type JSON 
            },
            withCredentials: true,
        })
            .then((response) => {
                console.log('response : ', response);
                setTouristData(response.data.items.item || []); //    
                setTotalCount(Number(response.data.totalCount));  // totalCount  
                setLoading(false); //   false 
            })

            .catch((error) => {
                console.error('Error fetching courses:', error);
                setLoading(false); //     false 
            });
    };

    //       
    const handleTouristClick = (contentId) => {
        setLoading(true); //  
        axios.get(`http://localhost:9000/tourist-info?id=${contentId}`)
            .then((response) => {

                const detailCommon = response.data;

                navigate('/tourist-info', { state: { detailCommon } }); //   
                setLoading(false);
            })
            .catch((error) => {
                console.error('Error fetching course info:', error);
                setLoading(false);
            });
    };

    //    
    const createPageButtons = (totalPages) => {
        const maxVisibleButtons = 5; //      
        const currentBlock = Math.floor((currentPage - 1) / maxVisibleButtons); //   
        const startPage = currentBlock * maxVisibleButtons + 1; //    
        const endPage = Math.min(startPage + maxVisibleButtons - 1, totalPages); //    

        const buttons = [];

        // '' 
        if (currentPage > 1) {
            buttons.push(
                <button key="first" onClick={() => handlePageChange(1)}></button>
            );
        }

        // '' 
        if (currentBlock > 0) {
            buttons.push(
                <button key="prev" onClick={() => handlePageChange(startPage - 1)}></button>
            );
        }

        //     
        for (let i = startPage; i <= endPage; i++) {
            buttons.push(
                <button
                    key={i}
                    className={i === currentPage ? 'active' : ''}
                    onClick={() => handlePageChange(i)}
                >
                    {i}
                </button>
            );
        }

        // '' 
        if (endPage < totalPages) {
            buttons.push(
                <button key="next" onClick={() => handlePageChange(endPage + 1)}></button>
            );
        }

        // '' 
        if (currentPage < totalPages) {
            buttons.push(
                <button key="last" onClick={() => handlePageChange(totalPages)}></button>
            );
        }

        return buttons;
    };

    //   
    const handlePageChange = (page) => {
        setCurrentPage(page);
    };

    useEffect(() => {
        handleSearch(); //    
    }, [currentPage, arrange]); // c
    // urrentPage, arrange   

    const totalPages = totalCount ? Math.ceil(totalCount / 10) : 0; //   10 

    return (
        <div className="tourist-wrapper">
            {/*   */}
            <select
                value={categoryFilter}
                onChange={(e) => setCategoryFilter(e.target.value)}
                className="filter-select"
            >
                <option value=""> </option>
                {categoryOptions.map((option) => (
                    <option key={option.value} value={option.value}>
                        {option.label}
                    </option>
                ))}
            </select>

            {/*   */}
            <select
                value={regionFilter}
                onChange={(e) => setRegionFilter(e.target.value)}
                className="filter-select"
            >
                <option value=""> </option>
                {regionOptions.map((option) => (
                    <option key={option.value} value={option.value}>
                        {option.label}
                    </option>
                ))}
            </select>


            {/*   */}
            <input
                type="text"
                placeholder=" "
                value={searchKeyword}
                onChange={(e) => setSearchKeyword(e.target.value)}
                className="search-input"
            />

            <button onClick={handleSearch} className="search-button"></button>
            <div className="total-check">
                <p className="total-count"> {totalCount} </p>

                {/*   */}
                <select
                    className="sort-select"
                    value={arrange}
                    onChange={(e) => setArrange(e.target.value)} //  
                >
                    <option value="O"></option>
                    <option value="Q"></option>
                    <option value="R"></option>
                </select>
            </div>

            <div>
                {loading ? (
                    <p> ...</p>
                ) : (
                    <div className="travel-course-list-content">
                        {touristData.map((tourist) => {
                            const regionName = getRegionName(tourist.areacode); //   
                            const hashtag = getHashtag(tourist.cat3); //  

                            return (
                                <div key={tourist.contentid} className="travel-course-list" onClick={() => handleTouristClick(tourist.contentid)}>
                                    {tourist.firstimage && (
                                        <img
                                            src={tourist.firstimage}
                                            alt={tourist.title}
                                            className="travel-course-list__img"
                                        />
                                    )}
                                    <h4 className="course-title">{tourist.title}</h4>
                                    <div className="course-box">
                                        {/*  */}
                                        <p className="course-region">{regionName}</p>
                                        {/*   */}
                                        <p className="course-hashtag">{hashtag}</p>
                                    </div>
                                </div>
                            );
                        })}
                    </div>
                )}
            </div>

            {/*   */}
            <div className="pagination">
                {/*  */}
                {totalPages > 0 && createPageButtons(totalPages)}
            </div>
        </div>
    );
};

export default Tourist;
