import { useEffect, useState } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import axios from 'axios';
import '../css/Destination.scss';

const Destination = () => {
    const location = useLocation();
    const { plannerItem } = location.state || {}; // state  
    const plannerID = new URLSearchParams(location.search).get("plannerID");
    const [destinations, setDestinations] = useState([]);
    const [username, setUsername] = useState('');
    const [likes, setLikes] = useState(plannerItem ? plannerItem.likes : 0);  //  
    const [commentText, setCommentText] = useState(''); //   
    const [comments, setComments] = useState([]); //   

    const navigate = useNavigate();

    // destination    API 
    useEffect(() => {
        if (plannerID) {
            axios.get(`http://localhost:9000/planner/board/destination?plannerID=${plannerID}`)
                .then((response) => {
                    setUsername(response.data[0].username);
                    setDestinations(response.data);
                    setComments(response.data[0].comments || []);  //    
                })
                .catch((error) => {
                    console.error("Error fetching destinations:", error);
                });
        }
    }, [plannerID]);

    //     
    const linkToDestinationInfo = () => {
        navigate(`/planner/board/destination/info?plannerID=${plannerID}`);
    }

    //     
    const handleLikeClick = () => {
        axios.post(`http://localhost:9000/planner/like`, {
            plannerID,
            userID: 'currentUserID',  //    ID 
        })
            .then((response) => {
                setLikes(response.data.likes); //     
            })
            .catch((error) => {
                console.error("Error updating like:", error);
            });
    };

    //    
    const handleCommentSubmit = (e) => {
        e.preventDefault();
        if (!commentText.trim()) {
            return;  //    
        }

        axios.post(`http://localhost:9000/planner/board/comment`, {
            plannerID,
            userId: 'currentUserID',  //    ID
            comment: commentText,
        })
            .then((response) => {
                setComments((prevComments) => [...prevComments, response.data]); //   
                setCommentText(''); //   
            })
            .catch((error) => {
                console.error("Error adding comment:", error);
            });
    };

    return (
        <div className="Destination-wrapper">
            <div className="Destination-desc">
                <img src={plannerItem.thumbnailImage} alt={plannerItem.plannerTitle} />
                <h2>{plannerItem.plannerTitle}</h2>
                <p>{plannerItem.day - 1} {plannerItem.day}</p>
                <p> : {plannerItem.area}</p>
                <p>  : {plannerItem.description}</p>
                <p>: {likes}</p>
                <button onClick={handleLikeClick}></button>
                <button onClick={linkToDestinationInfo}>  </button>
                {/*        */}
                {/* <div></div>
                <div></div> */}
            </div>

            <div className="Destination-comments">
                <h3></h3>
                <ul>
                    {comments.map((comment, index) => (
                        <li key={index}>
                            <p>{comment.username}: {comment.text}</p>
                        </li>
                    ))}
                </ul>

                <form onSubmit={handleCommentSubmit}>
                    <textarea
                        value={commentText}
                        onChange={(e) => setCommentText(e.target.value)}
                        placeholder=" "
                    />
                    <button type="submit"> </button>
                </form>
            </div>
        </div>
    );
};

export default Destination;
