import { useState } from "react";
import axios from "axios";
import "./LoginForm.scss";

const LoginForm = () => {
  const [formData, setFormData] = useState({
    userid: "",
    password: "",
    rememberMe: false,
  });
  const [error, setError] = useState(null);

  //   
  const handleChange = (e) => {
    const { name, value, type, checked } = e.target;
    const fieldValue = type === "checkbox" ? checked : value;
    setFormData((prev) => ({ ...prev, [name]: fieldValue }));
  };

  const handleSocialLogin = (provider) => {
    window.location.href = `/oauth2/authorization/${provider}`;
  };

  const handleLogin = () => {
    console.log("  ");
    window.location.href = "http://localhost:9000/oauth2/authorization/naver";
  };
  

  // 
  const handleSubmit = async (e) => {
    e.preventDefault();
    console.log(formData);
    setError(null); //  
    try {
      const response = await axios.post(
        "http://localhost:9000/user/login",
        formData,
        {
          headers: {
            "Content-Type": "application/json",
          },
          withCredentials: true, // 
        }
      );
      console.log(" :", response.data);
      //   
      alert("  !");
    } catch (err) {
      console.log(" ,", err);
      setError(" .  ");
    }
  };

  return (
    <div className="wrapper">
      <div className="logo">  </div>
      <div className="login">
        <form onSubmit={handleSubmit}>
          <label htmlFor="userid"> ID:</label>
          <input
            type="text"
            id="userid"
            name="userid"
            value={formData.userid}
            onChange={handleChange}
            required
          />
          <label htmlFor="password">PASSWORD:</label>
          <input
            type="text"
            id="password"
            name="password"
            value={formData.password}
            onChange={handleChange}
            required
          />
          <div className="checkbox">
            <label>ID </label>
            <input
              type="checkbox"
              name="rememberMe"
              checked={formData.rememberMe}
              onChange={handleChange}
            />
          </div>
          {error && <p className="error-message">{error}</p>}
          <button className="loginButton" type="submit">
            
          </button>
        </form>
      </div>
      <div className="join">
          ? <a href="/user/join"></a>
      </div>
      <hr />
      <div className="oauth2">
        <div style={{ textAlign: "center" }}>SNS  </div>
        <div className="oauth2-buttons">
          <ul>
            <li>
              <a href="/oauth2/authorization/kakao" title="">
                <img src="/images/kakaobutton.png" alt="" />
              </a>
            </li>
            <li>
              <button
                className="oauth2-button"
                onClick={handleLogin}
              >
                <img src="/images/naverbutton.png" alt=" " />
              </button>
            </li>
            <li>
              <a href="/oauth2/authorization/google" title=" ">
                <img src="/images/googlebutton.png" alt=" " />
              </a>
            </li>
            <li>
              <a href="/oauth2/authorization/instagram" title="">
                <img src="/images/instabutton.png" alt=" " />
              </a>
            </li>
          </ul>
        </div>
      </div>
    </div>
  );
};

export default LoginForm;
