import {useState, useEffect} from 'react';

const Options = (props) => {
    //   checked 
    const [food, setFood] = useState(true);
    const [accom, setAccom] = useState(true);
    const [place, setPlace] = useState(true);

    //   check 
    const viewAll = () => {
        setFood(true);
        setAccom(true);
        setPlace(true);
    }
    //     checked  
    const viewFood = () => {setFood(!food)}
    const viewAccom = () => {setAccom(!accom)}
    const viewPlace = () => {setPlace(!place)}

    //         .
    useEffect(()=>{
        props.OptionData([{"food":food,"accom":accom,"place":place}])
    },[food,accom,place])

    return (
        <>
            <input
                type="button"
                name="all"
                id="all"
                onClick={viewAll}
                value=""
            />
            
            <label></label>
            <input
                type="checkbox"
                name="food"
                id="food"
                onChange={viewFood}
                checked={food}
            />

            <label></label>
            <input
                type="checkbox"
                name="accom"
                id="accom"
                onChange={viewAccom}
                checked={accom}
            />

            <label></label>
            <input
                type="checkbox"
                name="place"
                id="place"
                checked={place}
                onChange={viewPlace}
            />
        </>
    );
}

export default Options;